import React, {useState, useEffect} from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import firebase from './firebase.js';
import logo from './logo.svg';
import './App.css';

// STAR MATCH - V4

const StarsDisplay = props => (
	<>
    {utils.range(1, props.count).map(starId =>
      <div key={starId} className="star" />
    )}
  </>
);

const PlayNumber = props => (
  <>
    <button 
      className="number"
      style={{ backgroundColor: colors[props.status] }}
      onClick={() => props.onClick(props.number, props.status)}
    >
      {props.number}
    </button>

    <KeyboardEventHandler
    handleKeys={[props.number+""]}
    onKeyEvent={(key, e) => props.onClick(props.number, props.status)} />
  </>
);

const PlayAgain = props => (
  <div className ='game-done'>
    <div className="message" style ={{color: props.gameStatus == 'lost' ? 'red' : 'green'}}>
      {props.gameStatus == 'lost' ? 'Game Over' : 'Nice'}
    </div>
    <button onClick={props.onClick}>Play Again</button>
  </div>
)

const useGameState = () => {
  	const [stars, setStars] = useState(utils.random(1, 9));
  const [availableNums, setAvailableNums] = useState(utils.range(1, 9));
  const [candidateNums, setCandidateNums] = useState([]);

  const [username, setUsername] = useState('');

  const [secondsLeft, setSecondsLeft] = useState(10.01);
  
  useEffect(() => {
    if(secondsLeft > 0 && availableNums.length > 0){
    const timerId = setTimeout(() => {
      const roundedNum = secondsLeft - .01;
      setSecondsLeft(Math.round(100*roundedNum)/100);
    }, 10)
    return () => clearTimeout(timerId)
  }
  else{
    const secondsRef = firebase.database().ref('users/');

    var secondsObj = {
      seconds: secondsLeft,
    }
    // secondsRef.child.get

    secondsRef.child(username).set(secondsObj);
  }
  });
  
  const setGameState = (newCandidateNums, username) => {
    setUsername(username)
      if(utils.sum(newCandidateNums) !== stars){
      setCandidateNums(newCandidateNums);
    }
    else{
      const newAvailableNums = availableNums.filter(
        n => !newCandidateNums.includes(n)
      );
      setStars(utils.randomSumIn(newAvailableNums, 9));
      setAvailableNums(newAvailableNums);
      setCandidateNums([])
    }
  }

  
  return{stars, availableNums, candidateNums, secondsLeft, setGameState}
};

//////////////////////////////////////////////////////////////////////////////////////////////////////

const Game = (props) => {

  const{
    stars,
    availableNums,
    candidateNums,
    secondsLeft,
    setGameState,
  }
   = useGameState();

  const candidatesAreWrong = utils.sum(candidateNums) > stars;
  const gameStatus = availableNums.length == 0 ? 'won' : secondsLeft === 0 ? 'lost' : 'active'

  const numberStatus = (number) => {
  	if (!availableNums.includes(number)) {
    	return 'used';
    }
    if (candidateNums.includes(number)) {
    	return candidatesAreWrong ? 'wrong': 'candidate';
    }
    return 'available';
  };
  
  
  const onNumberClick = (number, currentStatus) => {
    if(gameStatus !== 'active' || currentStatus == 'used'){
      return;
    }
    const newCandidateNums = 
          currentStatus === 'available'
          ? candidateNums.concat(number)
          :candidateNums.filter(cn => cn !== number);
    
  setGameState(newCandidateNums, props.username);
  }

    return (

      <div className="game">
        <div className="help">
          Pick 1 or more numbers that sum to the number of stars
        </div>
        <div className="body">
          <div className="left">
            {gameStatus !== 'active' ? 
              (<PlayAgain onClick={props.startNewGame} gameStatus={gameStatus}/>) 
            : (<StarsDisplay count={stars}/>)}
          </div>
          <div className="right">
            {utils.range(1, 9).map(number =>
              <PlayNumber 
                key={number} 
                status={numberStatus(number)}
                number={number}
                onClick = {onNumberClick}
              />
            )}
          </div>
        </div>
        <div className="timer">Time Remaining: {secondsLeft}</div>
        <KeyboardEventHandler
      handleKeys={['enter']}
      onKeyEvent={(key, e) => props.startNewGame(props.username) } />
      </div>
    );
  

};
//////////////////////////////////////////////////////////////////////////////////////////////////////

const TheStart = (props) =>{

  const [username, setUsername] = useState('');

  const updateUsername = (e) =>{
    setUsername(e.target.value);
    };

  return(
    <>
  <div>Press Enter To Start</div>
  <input onChange={updateUsername}></input>
  <KeyboardEventHandler
    handleKeys={['enter']}
    // onKeyEvent={(key, e) => props.startNewGame()}
    onKeyEvent={(key, e) => props.startNewGame(username)}
     />
  </>
  );
};


//////////////////////////////////////////////////////////////////////////////////////////////////////

const StarMatch = () => {

const startNewGame = (theUsername) => {
  setUsername(theUsername);
  if(toggle === 0){
    setToggle(toggle + 1);
  }
  setGameId(gameId + 1)
}


  const [gameId, setGameId] = useState(1);
  const [toggle, setToggle] = useState(0);
  const [username, setUsername] = useState("");
  if(toggle === 0){
    return <TheStart startNewGame={startNewGame}/>
  }
  else{
    return <Game key={gameId} startNewGame={startNewGame} toggle={toggle} username={username}/>
  }
}


// Color Theme
const colors = {
  available: 'lightgray',
  used: 'lightgreen',
  wrong: 'lightcoral',
  candidate: 'deepskyblue',
};

// Math science
const utils = {
  // Sum an array
  sum: arr => arr.reduce((acc, curr) => acc + curr, 0),

  // create an array of numbers between min and max (edges included)
  range: (min, max) => Array.from({ length: max - min + 1 }, (_, i) => min + i),

  // pick a random number between min and max (edges included)
  random: (min, max) => min + Math.floor(max * Math.random()),

  // Given an array of numbers and a max...
  // Pick a random sum (< max) from the set of all available sums in arr
  randomSumIn: (arr, max) => {
    const sets = [[]];
    const sums = [];
    for (let i = 0; i < arr.length; i++) {
      for (let j = 0, len = sets.length; j < len; j++) {
        const candidateSet = sets[j].concat(arr[i]);
        const candidateSum = utils.sum(candidateSet);
        if (candidateSum <= max) {
          sets.push(candidateSet);
          sums.push(candidateSum);
        }
      }
    }
    return sums[utils.random(0, sums.length)];
  },
};


export default function App() {
  return (
    <StarMatch />
  );
}

